
.tued-carousel {
    position: relative;

    .tued-carousel-navigation {
        .tued-carousel-next,
        .tued-carousel-prev {
            width: 30px;
            height: 40px;
            position: absolute;
            top: 50%;
            transform: translateY(-50%);
            z-index: 10;

            &:hover {
                cursor: pointer;
            }
        }
        .tued-carousel-next {
            right: 20px;
        }
        .tued-carousel-prev {
            left: 20px;
        }
    }

    .tued-carousel-actions {
        background-color: #fff;
        padding: 7px;
        border: 1px solid #ddd;
        text-align: center;

        > div {
            display: inline-block;
            padding: 0 20px 0 7px;
        }
    }

    .tued-carousel-slide {
        display: none;
    }

    .tued-chevron {
        &:after {
            border-style: solid;
            border-width: 0.35em 0.35em 0 0;
            content: '';
            display: inline-block;
            height: 1.55em;
            position: relative;
            top: 0.15em;
            vertical-align: top;
            width: 1.55em;
            left: 0.25em;
            transform: rotate(-135deg);
        }

        &.tued-chevron-right:after {
            left: 0.15em;
            transform: rotate(45deg);
        }
    }
}
